import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".row-centered{text-align:center}.col-centered{display:inline-block;float:none;margin-right:-4px;text-align:left}.pos{position:top;top:240px}", "",{"version":3,"sources":["webpack://./resources/js/Pages/IPCR_Tracking/Index.vue"],"names":[],"mappings":"AAwXA,cACI,iBACJ,CAEA,cACI,oBAAqB,CACrB,UAAW,CAEX,iBAAkB,CADlB,eAEJ,CAEA,KACI,YAAa,CACb,SACJ","sourcesContent":["<template>\r\n\r\n    <Head>\r\n        <title>Home</title>\r\n    </Head>\r\n\r\n    <!--<p style=\"text-align: justify;\">Sed ut perspiciatis, unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam eaque ipsa, quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt, explicabo. Nemo enim ipsam voluptatem, quia voluptas sit, aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos, qui ratione voluptatem sequi nesciunt, neque porro quisquam est, qui dolorem ipsum, quia dolor sit amet consectetur.\r\n    </p>-->\r\n    <div class=\"row gap-20 masonry pos-r\">\r\n        <div class=\"peers fxw-nw jc-sb ai-c\">\r\n            <h3>IPCR Tracking</h3>\r\n            <!-- {{ emp_code }}\r\n            {{ data }} -->\r\n            <!-- {{ ipcr_codes }} -->\r\n            <div class=\"peers\">\r\n                <!-- <div class=\"peer mR-10\">\r\n\r\n                    <input v-model=\"search\" type=\"text\" class=\"form-control form-control-sm\" placeholder=\"Search...\">\r\n                </div> -->\r\n\r\n            </div>\r\n        </div>\r\n        <filtering v-if=\"filter\" @closeFilter=\"filter = false\" @change=\"filterData()\">\r\n            Filter by Date From\r\n            <input type=\"date\" v-model=\"date_from\" class=\"form-control\" />\r\n            Filter by Date To\r\n            <input type=\"date\" v-model=\"date_to\" class=\"form-control\" />\r\n            Search IPCR Code\r\n            <input v-model=\"ipcr_code_filter\" type=\"text\" class=\"form-control form-control-sm\" placeholder=\"Search...\">\r\n            <button class=\"btn btn-sm btn-danger mT-5 text-white\" @click=\"clearFilter\">Clear Filter</button>\r\n        </filtering>\r\n\r\n        <filtering v-if=\"filter_month\" @closeFilter=\"filter = false\" @change=\"filterData()\">\r\n            Filter By IPCR Code\r\n            <input type=\"text\" class=\"form-control\" />\r\n            Filter By Month\r\n            <select v-model=\"month_filter\" class=\"form-control\">\r\n                <option value=\"1\">January</option>\r\n                <option value=\"2\">February</option>\r\n                <option value=\"3\">March</option>\r\n                <option value=\"4\">April</option>\r\n                <option value=\"5\">May</option>\r\n                <option value=\"6\">June</option>\r\n                <option value=\"7\">July</option>\r\n                <option value=\"8\">August</option>\r\n                <option value=\"9\">September</option>\r\n                <option value=\"10\">October</option>\r\n                <option value=\"11\">November</option>\r\n                <option value=\"12\">December</option>\r\n            </select>\r\n            Filter By year\r\n            <input class=\"form-control\" v-model=\"year_filter\" />\r\n            <button class=\"btn btn-sm btn-danger mT-5 text-white\" @click=\"clearFilter\">Clear Filter</button>\r\n        </filtering>\r\n        <FilterPrinting v-if=\"filter_p\" @closeFilter=\"filter_p = false\">\r\n            Date From\r\n            <input type=\"date\" v-model=\"date_from\" class=\"form-control\" />\r\n            Date To\r\n            <input type=\"date\" v-model=\"date_to\" class=\"form-control\" />\r\n            <button class=\"btn btn-sm btn-primary mT-5 text-white\" @click=\"printSubmit\">Print Report</button>\r\n        </FilterPrinting>\r\n        <FilterPrinting v-if=\"filter_sync\" @closeFilter=\"filter_sync = false\">\r\n            Date From\r\n            <input type=\"date\" v-model=\"date_from\" class=\"form-control\" />\r\n            Date To\r\n            <input type=\"date\" v-model=\"date_to\" class=\"form-control\" />\r\n            <button class=\"btn btn-sm btn-primary mT-5 text-white\" @click=\"filterSyncing()\">Sync Daily</button>\r\n        </FilterPrinting>\r\n\r\n\r\n        <div class=\"masonry-sizer col-md-6\"></div>\r\n        <div class=\"masonry-item w-100\">\r\n            <div class=\"row gap-20\"></div>\r\n            <div class=\"bgc-white p-20 bd\">\r\n                <div class=\"table-responsive\">\r\n                    <table class=\"table table-sm table-borderless table-striped table-hover\">\r\n                        <thead>\r\n                            <tr style=\"background-color: #B7DEE8;\">\r\n                                <th style=\"width: 15%;\">Date</th>\r\n                                <th style=\"width: 10%;\">Semester</th>\r\n                                <th style=\"width: 5%;\">Month</th>\r\n                                <th style=\"width: 15%;\">Description</th>\r\n                                <th style=\"width: 30%;\">Remarks</th>\r\n                                <th style=\"width: 15%;\">Acted by</th>\r\n                            </tr>\r\n                        </thead>\r\n                        <tbody>\r\n                            <tr v-for=\"dat in data\">\r\n                                <td>{{ dat.formatted_created_at }}</td>\r\n                                <td>{{ setSem(dat.sem) + \" \" +dat.year }}</td>\r\n                                <td>{{ getMonthName(dat.month) == \"Invalid Month\"? \"\": getMonthName(dat.month) }}</td>\r\n                                <td>{{ SetType(dat.type) }}</td>\r\n                                <td>{{ dat.remarks }}</td>\r\n                                <td>{{ dat.employee_name }}</td>\r\n                            </tr>\r\n                        </tbody>\r\n                    </table>\r\n                </div>\r\n                <div class=\"row justify-content-center\">\r\n                    <div class=\"col-md-12\">\r\n                        <!-- <Pagination_Preserved :links=\"data.links\" /> -->\r\n                        <pagination :next=\"data.next_page_url\" :prev=\"data.prev_page_url\" />\r\n                    </div>\r\n                </div>\r\n                <div class=\"row justify-content-center\">\r\n                    <div class=\"col-md-12\">\r\n                        <p>\r\n                            {{ data.from }} to {{ data.to }} of\r\n                            {{ data.total }} entries\r\n                        </p>\r\n                    </div>\r\n                </div>\r\n\r\n            </div>\r\n        </div>\r\n        <Modal v-if=\"displayModal\" @close-modal-event=\"hideModal\">\r\n            <div class=\"d-flex justify-content-center\">\r\n                <iframe :src=\"my_link\" style=\"width:100%; height:450px\" />\r\n            </div>\r\n        </Modal>\r\n    </div>\r\n</template>\r\n<script>\r\nimport Filtering from \"@/Shared/Filter\";\r\nimport FilterPrinting from \"@/Shared/FilterPrint\";\r\nimport Pagination from \"@/Shared/Pagination\";\r\nimport Pagination_Preserved from \"@/Shared/Pagination_Preserved\";\r\nimport Modal from \"@/Shared/PrintModal\";\r\nexport default {\r\n    props: {\r\n        auth: Object,\r\n        emp_code: Object,\r\n        // mfos: Object,\r\n        data: Object,\r\n        ipcr_codes: Object,\r\n        // paps: Object,\r\n        // idpaps: String,\r\n        // functions: Object,\r\n        // filters: Object,\r\n    },\r\n    data() {\r\n        return {\r\n            // search: this.$props.filters.search,\r\n            filter: false,\r\n            filter_p: false,\r\n            filter_sync: false,\r\n            filter_month: false,\r\n            date_from: \"\",\r\n            date_to: \"\",\r\n            month_filter: \"\",\r\n            year_filter: \"\",\r\n            ipcr_code_filter: \"\",\r\n            displayModal: false,\r\n            my_link: \"\",\r\n            date: \"\",\r\n            // mfosel: \"\",\r\n        }\r\n    },\r\n    watch: {\r\n        //     search: _.debounce(function (value) {\r\n        //     this.$inertia.get(\r\n        //         \"/AddAccomplishment\",\r\n        //         { search: value },\r\n        //         {\r\n        //             preserveScroll: true,\r\n        //             preserveState: true,\r\n        //             replace: true,\r\n        //         }\r\n        //     );\r\n        // }, 300),\r\n        ipcr_code_filter: _.debounce(function (value) {\r\n            this.$inertia.get(\r\n                \"/Daily_Accomplishment/\",\r\n                {\r\n                    date_from: this.date_from,\r\n                    date_to: this.date_to,\r\n                    date: this.date,\r\n                    month: this.month_filter,\r\n                    year: this.year_filter,\r\n                    ipcr_code: value\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        }, 300),\r\n\r\n    },\r\n    components: {\r\n        Pagination, Filtering, Modal, FilterPrinting, paginationPreserved: Pagination_Preserved\r\n    },\r\n    mounted() {\r\n        this.setYear();\r\n    },\r\n    methods: {\r\n        SetType(Type){\r\n            var type = \"\";\r\n            if (Type === \"approve accomplishment\") {\r\n                type = \"Approved Accomplishment\"\r\n            } else if (Type === \"approve semestral accomplishment\"){\r\n                type = \"Approved Semestral Accomplishment\"\r\n            } else if (Type === \"approve target\"){\r\n                type = \"Approved Target\"\r\n            } else if (Type === \"final approve accomplishment\"){\r\n                type = \"Final Approved Accomplishment\"\r\n            } else if (Type === \"Recall IPCR semestral target\"){\r\n                type = \"Recall IPCR Semestral Target\"\r\n            } else if (Type === \"Recall Monthly Accomplishment\"){\r\n                type = \"Recall Monthly Accomplishment\"\r\n            } else if (Type === \"return accomplishment\"){\r\n                type = \"Returned Accomplishment\"\r\n            } else if (Type === \"return target\"){\r\n                type = \"Return Target\"\r\n            } else if (Type === \"review accomplishment\"){\r\n                type = \"Reviewed Accomplishment\"\r\n            } else if (Type === \"review semestral accomplishment\"){\r\n                type = \"Reviewed Semestral Accomplishment\"\r\n            } else if (Type === \"review target\"){\r\n                type = \"Reviewed Target\"\r\n            } else if (Type === \"Submit Monthly Accomplishment\"){\r\n                type = \"Submit Monthly Accomplishment\"\r\n            } else if (Type === \"Submitted semestral target\"){\r\n                type = \"Submitted Semestral Target\"\r\n            }\r\n\r\n            return type;\r\n        },\r\n        setSem(sem){\r\n            var semester = \"\";\r\n            if(sem == 1){\r\n            semester = \"January to June\"\r\n            } else {\r\n            semester = \"July to December\"\r\n            }\r\n\r\n            return semester;\r\n        },\r\n        setYear() {\r\n            var yr = new Date().getFullYear();\r\n            this.year_filter = yr;\r\n        },\r\n        showFilter() {\r\n\r\n            this.filter = !this.filter\r\n        },\r\n        showFilterP() {\r\n            // alert(\"show filter\");\r\n            this.filter_p = !this.filter_p\r\n        },\r\n\r\n        showFilterSync() {\r\n            // alert(\"show filter\");\r\n            this.filter_sync = !this.filter_sync\r\n        },\r\n        showCreate() {\r\n            this.$inertia.get(\r\n                \"/targets/create\",\r\n                {\r\n                    raao_id: this.raao_id\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        deleteOutput(id) {\r\n            let text = \"WARNING!\\nAre you sure you want to delete this Accomplishment?\" + id;\r\n            if (confirm(text) == true) {\r\n                this.$inertia.delete(\"/Daily_Accomplishment/\" + id);\r\n            }\r\n        },\r\n        getAccomplishment(tar_id) {\r\n            this.$inertia.get(\r\n                \"/accomplishments\",\r\n                {\r\n                    idtarget: tar_id\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        getPercent(accomp, targqty) {\r\n            var accSum = 0;\r\n            accomp.forEach(myFunction);\r\n            function myFunction(item) {\r\n                accSum += parseFloat(item.accomplishment_qty)\r\n\r\n            }\r\n            var percentt = (accSum / targqty) * 100\r\n            percentt = this.format_number(percentt, 2, true)\r\n            return percentt;\r\n        },\r\n        printSubmit() {\r\n            // alert(this.emp_code);\r\n            //var office_ind = document.getElementById(\"selectOffice\").selectedIndex;\r\n\r\n            // this.office =this.auth.user.office.office;\r\n            // var pg_head = this.functions.DEPTHEAD;\r\n            // var forFFUNCCOD = this.auth.user.office.department_code;\r\n            this.my_link = this.viewlink(this.emp_code, this.date_from, this.date_to);\r\n\r\n            this.showModal();\r\n        },\r\n\r\n        viewlink(username, date_from, date_to) {\r\n            //var linkt =\"abcdefghijklo534gdmoivndfigudfhgdyfugdhfugidhfuigdhfiugmccxcxcxzczczxczxczxcxzc5fghjkliuhghghghaaa555l&&&&-\";\r\n            var linkt = \"http://\";\r\n            var jasper_ip = this.jasper_ip;\r\n            var jasper_link = 'jasperserver/flow.html?pp=u%3DJamshasadid%7Cr%3DManager%7Co%3DEMEA%2CSales%7Cpa1%3DSweden&_flowId=viewReportFlow&_flowId=viewReportFlow&ParentFolderUri=%2Freports%2FIPCR%2FDaily_Accomplishment&reportUnit=%2Freports%2FIPCR%2FDaily_Accomplishment%2FIPCR_Daily&standAlone=true&decorate=no&output=pdf';\r\n            var params = '&username=' + username + '&date_from=' + date_from + '&date_to=' + date_to;\r\n            var linkl = linkt + jasper_ip + jasper_link + params;\r\n\r\n            return linkl;\r\n        },\r\n        showModal() {\r\n            this.displayModal = true;\r\n        },\r\n        hideModal() {\r\n            this.displayModal = false;\r\n        },\r\n        async filterData() {\r\n\r\n            this.$inertia.get(\r\n                \"/Daily_Accomplishment/\",\r\n                {\r\n                    date_from: this.date_from,\r\n                    date_to: this.date_to,\r\n                    date: this.date,\r\n                    month: this.month_filter,\r\n                    year: this.year_filter,\r\n                    ipcr_code: this.ipcr_code_filter\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n        clearFilter() {\r\n            this.date = \"\";\r\n            this.search = \"\";\r\n            this.month_filter = \"\";\r\n            this.year_filter = \"\";\r\n            this.ipcr_code_filter = \"\";\r\n            this.date_to = \"\";\r\n            this.date_from = \"\";\r\n            this.filterData();\r\n        },\r\n\r\n        async filterSyncing() {\r\n            this.$inertia.get(\r\n                \"/Daily_Accomplishment/sync_daily/PM\",\r\n                {\r\n                    date_from: this.date_from,\r\n                    date_to: this.date_to,\r\n                },\r\n                {\r\n                    preserveScroll: true,\r\n                    preserveState: true,\r\n                    replace: true,\r\n                }\r\n            );\r\n        },\r\n\r\n    }\r\n};\r\n</script>\r\n<style>\r\n.row-centered {\r\n    text-align: center;\r\n}\r\n\r\n.col-centered {\r\n    display: inline-block;\r\n    float: none;\r\n    text-align: left;\r\n    margin-right: -4px;\r\n}\r\n\r\n.pos {\r\n    position: top;\r\n    top: 240px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
